import React, { Component } from 'react';
import firebase from '../config/firebase';

export default class NovoLivro extends Component {

    constructor(props){
        super();
        this.state = {
            nome: ''
        }
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(e){
        e.preventDefault();
        const itemsRef = firebase.database().ref('livros');
        const item = {
            nome: this.state.nome,
            up: 0,
            down: 0
        }
        itemsRef.push(item);
        this.setState({nome: ""});
    }

    onChange(ev, field){
        this.setState({ [field]: ev.target.value });
    }

    render(){

        return(
            <div className="NovoLivro">
                <form onSubmit={this.handleSubmit}>
                    <label>Não encontrou o seu preferido? Adicione um:</label>
                    <br />
                    <input
                        onChange={(e) => this.onChange(e, 'nome')}
                        value={this.state.nome} />
                    <button
                        type="submit">
                        Adicionar    
                    </button>
                </form>
            </div>
        );

    }

}


