import React, { Component } from 'react';
import firebase from '../config/firebase';

export default class ListLivros extends Component {

    constructor(props){
        super();
        this.state = {
            livros: [],
            votedUp: localStorage.getItem('votedUp'),
            votedDown: localStorage.getItem('votedDown')
        }
        this.voteUp = this.voteUp.bind(this);
        this.voteDown = this.voteDown.bind(this);
    }

    updateLocalStorage({ id, type }){
        switch(type){
            case "WRITE_UP":
                localStorage.setItem('votedUp', id);
                this.setState({ votedUp: id });
                break;
            case "CLEAN_UP":
                localStorage.removeItem('votedUp');
                this.setState({ votedUp: null });
                break;
            case "WRITE_DOWN":
                localStorage.setItem('votedDown', id);
                this.setState({ votedDown: id });
                break;
            case "CLEAN_DOWN":
                localStorage.removeItem('votedDown');
                this.setState({ votedDown: null });
                break;
            default: 
                return;

        }
    }

    voteUp(livro){
        if( this.state.votedUp && this.state.votedUp !== livro.id ) return;
        if( !this.state.votedUp ){
            firebase.database().ref('livros/'+livro.id).update({ up: livro.up + 1 });
            this.updateLocalStorage({id: livro.id, type: "WRITE_UP"});
        } else {
            firebase.database().ref('livros/'+livro.id).update({ down: livro.down + 1 });
            this.updateLocalStorage({id: livro.id, type: "CLEAN_UP"});
        }
        
    }

    voteDown(livro){
        if( this.state.votedDown && this.state.votedDown !== livro.id ) return;
        if( !this.state.votedDown ){
            firebase.database().ref('livros/'+livro.id).update({ down: livro.down + 1 });
            this.updateLocalStorage({id: livro.id, type: "WRITE_DOWN"});
        } else {
            firebase.database().ref('livros/'+livro.id).update({ up: livro.up + 1 });
            this.updateLocalStorage({id: livro.id, type: "CLEAN_DOWN"});
        }
    }

    componentDidMount(){
        const itemsRef = firebase.database().ref('livros');
        itemsRef.on('value', (snapshot) => {
            let livros = snapshot.val();
            let newState = [];
            for(let livro in livros){
                newState.push({
                    id:livro,
                    nome: livros[livro].nome,
                    vote: (livros[livro].up || 0) - (livros[livro].down || 0),
                    up: livros[livro].up || 0,
                    down: livros[livro].down || 0
                });
            }
            this.setState({
                livros: newState
            });
        });
    }

    render(){

        return(
            <div className="ListLivros">
                <div>
                    {
                        this.state.livros
                        .sort((a,b) => a.vote < b.vote)
                        .map(livro => {
                            return (
                                <div className="container container-white livro" key={livro.id}>
                                    <span className="livro-nome">{livro.nome}</span>
                                    <div className="livro-data">
                                        <span className="livro-vote">{livro.vote}</span>
                                        <span
                                            className={this.state.votedUp === livro.id ? "pointer livro-up-select" : "pointer livro-up" }
                                            onClick={() => this.voteUp(livro)}>
                                            <i className="icon-thumbs-up-alt" aria-hidden="true"></i>
                                        </span>                                    
                                        <span
                                            className={this.state.votedDown === livro.id ? "pointer livro-down-select" : "pointer livro-down" }
                                            onClick={() => this.voteDown(livro)}>
                                            <i className="icon-thumbs-down-alt" aria-hidden="true"></i>
                                        </span>
                                    </div>
                                </div>
                            );
                        })
                    }
                </div>
            </div>
        );

    }

}


